  stroke-width: 31.8309886184;
  stroke-dasharray: 0,0,0,100;
  stroke-dashoffset: 25;
  animation: pie1 2s infinite ease both;
  animation-iteration-count: 1;
}

.pie1 {
  stroke: #822917;
}

.pie2 {
  stroke: #d94426;
  animation-name: pie2;
}

.pie3 {
  stroke: #571b0f;
  animation-name: pie3;
}

.pie4 {
  stroke: #b3b3b3;
  animation-name: pie4;
}

/*
 pie percentage made by stroke-dasharray.
 stroke-dasharray format is...

 keyframe 100% : 0 offset this-percentage rest

 I added keyframe 50% for infinite presentation.
*/
/* 1st pie is 40% */
@keyframes pie1 {
  50%, 100% {
    stroke-dasharray: 50,50,0,0;
  }
}

/* 2nd pie is 30% */
@keyframes pie2 {
  50%, 100% {
    stroke-dasharray: 0,50,20,30;
  }
}

/* 3rd pie is 30% */
@keyframes pie3 {
  50%, 100% {
    stroke-dasharray: 0,70,20,10;
  }
}

@keyframes pie4 {
  50%, 100% {
    stroke-dasharray: 0,90,10,0;
  }
}
